/*
 * {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
} */
@import url('https://fonts.googleapis.com/css?family=VT323&display=swap');

html, body, #root {
  background: #171b13;
  background: #191d2b;
  height: 100%;
}

body, input, button {
  font-family: 'VT323', monospace;
}

/* ############# */
h1 {
  color: #7ECC29;
  font-weight: normal;
  text-transform: uppercase;
}

ul {
  list-style: none;
}

.note {
  color: #7ECC29;
  padding-top: 9px;
  font-size: 16px;
}

.error {
  color: white;
  background:lightcoral;
  font-size: 20px;

  border: 2px solid red;
  border-radius: 8px;

  padding: 5px;
  margin-bottom: 10px;
}
